import React, { Component } from "react"

import Layout from "../components/layout/layout"
import SEO from "../components/seo"
import poster from '../images/about_poster.jpg'

class IndexPage extends Component{
  state = {
    day: 0,
    hour: 0,
    minute: 0,
    second: 0
  }

  componentDidMount () {
    const oldDate = '2018-04-09' 

    setInterval(() => {
      let newDate = new Date().getTime()
      let date = newDate - Date.parse(oldDate)

      this.setState({
        day: Math.floor(date / (24 * 3600 * 1000)),
        hour: new Date().getHours(),
        minute: new Date().getMinutes(),
        second: new Date().getSeconds()
      })

    }, 1000);
  }

  render () {
    const { day, hour, minute, second } = this.state

    return (
      <Layout>
        <SEO title="我 👻" />
        <div className={ 'about' }> 
          <h1>我</h1>
          <div className={ 'about__imgBox' }>
            <img src={ poster } alt={'图片'}/>
          </div>
          <div className={ 'about__content' } style={{ color: '#999' }}>
            <p>一名一直在努力的前端开发程序员, 接触前端已经 { day }天{ hour }小时{ minute }分钟{ second }秒 ⏰</p>
            <p>致力于成为新前端技术的第N批试玩玩家, 拒绝兼容古老的浏览器, 就让IE消失在历史中吧 ❌</p>
            <p>钢之炼金术师的死忠粉, 完全同意钢炼中 '等价交换' 这一规则 🔓</p>
            <p>高度拖延症患者, 我要看看我自己把博客完善完要拖多久 😎</p>
            <p>喜欢把自己懂的东西分享给别人, 鲁迅说: 自己懂了和写出来是两回事 😤</p>
            <p>吉他试玩玩家 / 跑步爱好玩家 / 莫名其妙爱上 🍎 的生态 / 没钱也要喝奶茶玩家 </p>
            <p>欢迎来掘金找我玩: <a target={ '_blank' } style={{ color: '#f80' }} href={ 'https://juejin.im/user/5b5f19f5f265da0f716c344c' }>@椒麻鸡拌面</a></p>
            <p>欢迎来github找我玩: <a target={ '_blank' } style={{ color: '#f80' }} href={ 'https://github.com/xuyede/xuyede.github.io' }>@xuyede</a></p>
          </div>
        </div>
        
      </Layout>
    )
  }
}

export default IndexPage