import React from 'react'
import { useStaticQuery, graphql, Link } from 'gatsby'
import logo from '../../images/blog-icon-white.png'
import './header.min.css'


export default function header({ open, toggleOpen }) {
  const data = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          title,
          nav
        }
      }
    }
  `)

  return (
    <header id={ 'header' } className={ 'header' }>
      <div className={ 'header__wrapper' }>
        <Link className={ 'header__link' } to={ '/' }>
          <img className={ 'main__img' } src={ logo } alt={ 'logo' } />
          <span>{ data.site.siteMetadata.title }</span>
        </Link>

        <div className={ `header__menu ${open ? 'open' : ''}` } onClick={ toggleOpen }>
          <span></span>
          <span></span>
          <span></span>
        </div>

        <nav className={ `header__nav ${open ? 'open' : ''}` }>
          <ul>
            {
              data.site.siteMetadata.nav.map((it, index) => {
                // 获取 nav名字和对应路由地址
                const [name, to] = it.split('|')
                return (
                  <li key={ index }>
                    <Link to={ to }>{ name }</Link>
                  </li>
                )
              })
            }
          </ul>
        </nav>
      </div>
    </header>
  )
}


